
'use client';

import React, { useEffect, useState } from 'react';
import { collection, query, where, orderBy, onSnapshot } from 'firebase/firestore';
import { db } from '@/lib/firebase';
import { useAuth } from '@/context/auth-context';
import { Order } from '@/functions/src/types';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Loader2 } from 'lucide-react';
import { Badge } from '@/components/ui/badge';
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from '@/components/ui/accordion';

export const OrdersHistoryTab = () => {
    const { user } = useAuth();
    const [orders, setOrders] = useState<Order[]>([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        if (!user) {
            setLoading(false);
            return;
        }

        const ordersQuery = query(
            collection(db, 'orders'),
            where('buyerUid', '==', user.uid),
            orderBy('createdAt', 'desc')
        );

        const unsubscribe = onSnapshot(ordersQuery, (snapshot) => {
            const ordersData = snapshot.docs.map(doc => ({ ...doc.data(), id: doc.id })) as Order[];
            setOrders(ordersData);
            setLoading(false);
        }, (error) => {
            console.error("Error fetching orders:", error);
            setLoading(false);
        });

        return () => unsubscribe();
    }, [user]);

    if (loading) {
        return <div className="flex justify-center py-10"><Loader2 className="h-8 w-8 animate-spin" /></div>;
    }

    if (orders.length === 0) {
        return <p className="text-center text-muted-foreground py-10">У вас ще немає оформлених замовлень.</p>;
    }

    const getStatusVariant = (status: Order['status']) => {
        switch (status) {
            case 'new': return 'default';
            case 'processing': return 'secondary';
            case 'shipped': return 'outline';
            case 'completed': return 'default';
            case 'cancelled': return 'destructive';
            default: return 'secondary';
        }
    };

    return (
        <Card>
            <CardHeader>
                <CardTitle>Історія Замовлень</CardTitle>
            </CardHeader>
            <CardContent>
                <Accordion type="single" collapsible className="w-full">
                    {orders.map(order => (
                        <AccordionItem value={order.id} key={order.id}>
                            <AccordionTrigger>
                                <div className='flex justify-between w-full pr-4'>
                                    <span>Замовлення №{order.id.substring(0, 6)}...</span>
                                    <span>{new Date(order.createdAt).toLocaleDateString('uk-UA')}</span>
                                    <span>{order.totalAmount.toFixed(2)} грн</span>
                                    <Badge variant={getStatusVariant(order.status)}>{order.status}</Badge>
                                </div>
                            </AccordionTrigger>
                            <AccordionContent>
                                <div className="p-4 bg-muted/50 rounded-lg">
                                    <h4 className="font-semibold mb-2">Деталі замовлення:</h4>
                                    <p><strong>Статус:</strong> {order.status}</p>
                                    <p><strong>Адреса доставки:</strong> {`${order.shippingInfo.city}, ${order.shippingInfo.department}`}</p>
                                    <h5 className="font-semibold mt-3 mb-1">Лоти в замовленні:</h5>
                                    <ul>
                                        {order.lots.map(lot => (
                                            <li key={lot.id}>- {lot.name} ({lot.finalPrice?.toFixed(2)} грн)</li>
                                        ))}
                                    </ul>
                                </div>
                            </AccordionContent>
                        </AccordionItem>
                    ))}
                </Accordion>
            </CardContent>
        </Card>
    );
};
